					<b-icon icon="person" font-scale="1.5" class="text-primary" v-else></b-icon>
					<strong class="fs-sm mx-2">{{ invite.name }}</strong>
				</div>
				<div class="d-flex align-items-center">
					<div v-if="invite.image">
						<b-icon icon="card-image" font-scale="1.1" style="color: #bf7b2780" :id="invite.id.toString()"></b-icon>
						<LinkImageTooltip :image="invite.image" :target="invite.id.toString()" />
					</div>
					<div class="d-block d-md-none">
						<b-dropdown variant="link" toggle-class="text-decoration-none p-0 box-shadow-none" right no-caret menu-class="p-0 shadow border-0">
							<template #button-content>
								<div class="btn_icon ml-2" v-b-tooltip.hover :title="$t('moreActions')">
									<b-icon icon="three-dots-vertical" class="text-muted" font-scale="1.5"></b-icon>
								</div>
							</template>
							<b-dropdown-item link-class="p-3" v-if="!isOneOnMany">
								<div class="d-flex align-items-center">
									<b-icon icon="person-plus" class="mr-3" width="1em" font-scale="1.4"></b-icon>
									<span>{{ $t("createPersonalizedInvite") }}</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
