									<span>{{ $t("copyLink") }}</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="envelope" class="mr-3" width="1em"></b-icon>
									<span>{{ $t("copyTimesToEmail") }}</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="code" class="mr-3" width="1em" font-scale="1.6"></b-icon>
									<span>{{ $t("embed") }}</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="pencil" class="mr-3" width="1em"></b-icon>
									<span>{{ $t("edit") }}</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="files" class="mr-3" width="1em"></b-icon>
									<span>{{ $t("clone") }}</span>
