					</div>
				</div>
			</div>

			<div class="link-card-body ml-3 pl-2 fs-sm text-muted">
				<div class="voted my-1">{{ durationTxt }}</div>
				<div class="my-1">{{ locationName | toCapitalize }}</div>
			</div>

			<div class="link-card-footer mt-auto py-3 d-none d-md-flex justify-content-end">
				<div class="btn_icon mx-2" v-b-tooltip.hover :title="$t('createPersonalizedInvite')" v-if="!isOneOnMany">
					<b-icon icon="person-plus" class="text-muted" font-scale="1.3"></b-icon>
				</div>
				<div class="btn_icon mx-2" v-b-tooltip.hover :title="$t('copyLink')">
					<b-icon icon="link" font-scale="1.8" class="text-muted"></b-icon>
				</div>
				<div class="btn_icon mx-2" v-b-tooltip.hover :title="$t('edit')">
					<b-icon icon="pencil" class="text-muted" font-scale="1.3"></b-icon>
				</div>
				<b-dropdown variant="link" toggle-class="text-decoration-none p-0 box-shadow-none" right no-caret menu-class="p-0 shadow border-0">
					<template #button-content>
						<div class="btn_icon ml-2" v-b-tooltip.hover :title="$t('moreActions')">
							<b-icon icon="three-dots-vertical" class="text-muted" font-scale="1.5"></b-icon>
						</div>
					</template>
					<b-dropdown-item link-class="p-3">
						<div class="d-flex align-items-center">
							<b-icon icon="envelope" class="mr-3" width="1em"></b-icon>
							<span>{{ $t("copyTimesToEmail") }}</span>
						</div>
					</b-dropdown-item>
					<b-dropdown-item link-class="p-3">
						<div class="d-flex align-items-center">
							<b-icon icon="code" class="mr-3" width="1em" font-scale="1.6"></b-icon>
							<span>{{ $t("embed") }}</span>
						</div>
					</b-dropdown-item>
					<b-dropdown-item link-class="p-3">
						<div class="d-flex align-items-center">
							<b-icon icon="files" class="mr-3" width="1em"></b-icon>
							<span>{{ $t("clone") }}</span>
						</div>
					</b-dropdown-item>
					<b-dropdown-item link-class="p-3">
						<div class="d-flex align-items-center">
							<b-icon icon="trash" class="mr-3" width="1em"></b-icon>
							<span>{{ $t("delete") }}</span>
						</div>
					</b-dropdown-item>
				</b-dropdown>
			</div>
		</div>
	</b-card>
</template>

<script>
	import LinkImageTooltip from "@/components/LinkImageTooltip";

	export default {
		components: { LinkImageTooltip },

		props: { invite: { type: Object, required: true } },

		computed: {
			isOneOnMany() {
				return this.invite.type == "oneOnMany";
			},

			durationTxt() {
				let duration = this.invite.duration;

				if (duration <= 30) return "30 min";

				let hours = duration / 60;

				let minutes = duration % 60;

				if (minutes == 0) {
					return `${hours} hr`;
				} else {
					return `${hours.toString().split(".")[0]} hr ${minutes} min`;
				}
			},

			locationName() {
				if (!this.invite.location?.type) return "";

				if (this.invite.location.name == "custom") {
					return this.invite.location.text;
				}

				return this.invite.location.name;
			},
		},
	};
</script>
