<template>
	<b-card body-class="pt-2 px-2 pb-2 pb-md-0" class="rounded-lg h-100">
		<div class="link-card pt-1 px-1 pb-1 pb-md-0 h-100 d-flex flex-column">
			<div class="link-card-header d-flex w-100 align-items-center justify-content-between">
				<div class="text-truncate">
					<b-icon icon="calendar2-event" font-scale="1.4" class="text-primary"></b-icon>
					<router-link class="text-decoration-none text-dark mx-2" :to="{ name: 'event', params: { id: event.id } }">
						<strong class="fs-sm">{{ eventTitle }}</strong>
					</router-link>
				</div>
				<div class="d-block d-md-none">
					<b-dropdown variant="link" toggle-class="text-decoration-none p-0 box-shadow-none" right no-caret menu-class="p-0 shadow border-0">
						<template #button-content>
							<div class="btn_icon ml-2" v-b-tooltip.hover :title="$t('moreActions')">
								<b-icon icon="three-dots-vertical" class="text-muted" font-scale="1.5"></b-icon>
							</div>
						</template>
						<b-dropdown-item link-class="p-3" :to="{ name: 'event', params: { id: event.id } }">
							<div class="d-flex align-items-center">
								<b-icon icon="calendar2-date" class="mr-3" width="1em" font-scale="1.4"></b-icon>
								<span>{{ $t("viewEventDetails") }}</span>
							</div>
						</b-dropdown-item>
						<b-dropdown-item link-class="p-3" v-if="isOneOnOne">
							<div class="d-flex align-items-center">
								<b-icon icon="calendar2-event" class="mr-3" width="1em" font-scale="1.4"></b-icon>
								<span>{{ $t("reschedule") }}</span>
							</div>
						</b-dropdown-item>
						<b-dropdown-item link-class="p-3">
							<div class="d-flex align-items-center">
								<b-icon icon="x-circle" class="mr-3" width="1em"></b-icon>
								<span>{{ $t("cancel") }}</span>
							</div>
						</b-dropdown-item>
					</b-dropdown>
				</div>
			</div>

			<div class="link-card-body ml-3 pl-2 fs-sm text-muted">
				<div class="voted my-1">{{ eventDate }}</div>
				<div class="my-1">{{ eventFrom }} - {{ eventTo }}</div>
			</div>

			<div class="link-card-footer mt-auto py-3 d-none d-md-flex justify-content-end">
				<router-link class="btn_icon mx-2" v-b-tooltip.hover :title="$t('viewEventDetails')" :to="{ name: 'event', params: { id: event.id } }">
					<b-icon icon="calendar2-date" class="text-muted" font-scale="1.5"></b-icon>
				</router-link>
				<!-- </div> -->
				<div class="btn_icon mr-2" v-b-tooltip.hover :title="$t('reschedule')" v-if="isOneOnOne">
					<b-icon icon="calendar2-event" class="text-muted" font-scale="1.5"></b-icon>
				</div>
				<div class="btn_icon" v-b-tooltip.hover :title="$t('cancel')">
					<b-icon icon="x-circle" class="text-muted" font-scale="1.5"></b-icon>
				</div>
			</div>
		</div>
	</b-card>
</template>

<script>
	export default {
		props: { event: { type: Object, required: true } },

		computed: {
			eventDate() {
				let date = new Date(this.event.startDate);

				date = date.toLocaleDateString("en-US", { weekday: "short", month: "short", day: "numeric" });

				return date;
			},

			eventFrom() {
				let date = new Date(this.event.startDate);

				date = date.toLocaleTimeString("en-US", { hour: "numeric", minute: "2-digit" });

				return date.toLowerCase();
			},

			eventTo() {
				let duration = this.event.duration * 60 * 1000;

				let date = new Date(this.event.startDate);

				date = date.getTime() + duration;

				date = new Date(date);

				date = date.toLocaleTimeString("en-US", { hour: "numeric", minute: "2-digit" });

				return date.toLowerCase();
			},

			isOneOnOne() {
				return this.event.invite.type == "oneOnOne";
			},

			eventTitle() {
				if (this.isOneOnOne) {
					let invitee = this.event.invitees.find((invitee) => !invitee.isGuest);

					let organizer = this.event.invite.organizer.firstName + " " + this.event.invite.organizer.lastName;

					return invitee.name + " and " + organizer;
				}

				return this.event.invite.name;
			},
		},
	};
</script>
