	<b-card body-class="pt-2 px-2 pb-2 pb-md-0" class="rounded-lg h-100">
		<div class="link-card pt-1 px-1 pb-1 pb-md-0 h-100 d-flex flex-column">
			<div class="link-card-header d-flex align-items-center justify-content-between">
				<div class="text-truncate">
					<b-icon icon="calendar2-week" class="fs-default text-primary mr-2"></b-icon>
					<strong class="fs-sm">{{ poll.name }}</strong>
				</div>
				<div class="d-flex align-items-center">
					<div v-if="poll.image">
						<b-icon icon="card-image" font-scale="1.1" style="color: #bf7b2780" :id="poll.id.toString()"></b-icon>
						<LinkImageTooltip :image="poll.image" :target="poll.id.toString()" />
					</div>
					<div class="d-block d-md-none">
						<b-dropdown variant="link" toggle-class="text-decoration-none p-0 box-shadow-none" right no-caret menu-class="p-0 shadow border-0">
							<template #button-content>
								<div class="btn_icon ml-2" v-b-tooltip.hover title="More actions">
									<b-icon icon="three-dots-vertical" class="text-muted" font-scale="1.5"></b-icon>
								</div>
							</template>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="link" class="mr-3" width="1em" font-scale="1.4"></b-icon>
									<span>Copy Link</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="calendar2-check" class="mr-3" width="1em"></b-icon>
									<span>View Poll Status</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="pencil" class="mr-3" width="1em"></b-icon>
									<span>Edit</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="files" class="mr-3" width="1em"></b-icon>
									<span>Clone</span>
								</div>
							</b-dropdown-item>
							<b-dropdown-item link-class="p-3">
								<div class="d-flex align-items-center">
									<b-icon icon="trash" class="mr-3" width="1em"></b-icon>
									<span>Delete</span>
								</div>
							</b-dropdown-item>
						</b-dropdown>
					</div>
				</div>
			</div>

			<div class="link-body ml-3 pl-2 fs-sm text-muted">
				<div class="voted my-1">{{ voted }} People voted</div>
				<span :class="`d-inline-block my-1 remaining rounded ${remaining.variant}`">{{ remaining.text }}</span>
			</div>

			<div class="link-card-footer mt-auto py-3 d-none d-md-flex justify-content-end">
				<div class="btn_icon mx-2" v-b-tooltip.hover title="Copy Link">
					<b-icon icon="link" font-scale="1.8" class="text-muted"></b-icon>
				</div>
				<div class="btn_icon mx-2" v-b-tooltip.hover title="View Poll Status">
					<b-icon icon="calendar2-check" class="text-muted" font-scale="1.3"></b-icon>
				</div>
				<div class="btn_icon mx-2" v-b-tooltip.hover title="Edit">
					<b-icon icon="pencil" class="text-muted" font-scale="1.3"></b-icon>
				</div>
				<b-dropdown variant="link" toggle-class="text-decoration-none p-0 box-shadow-none" right no-caret menu-class="p-0 shadow border-0">
					<template #button-content>
						<div class="btn_icon ml-2" v-b-tooltip.hover title="More actions">
							<b-icon icon="three-dots-vertical" class="text-muted" font-scale="1.5"></b-icon>
						</div>
					</template>
					<b-dropdown-item link-class="p-3">
						<div class="d-flex align-items-center">
							<b-icon icon="files" class="mr-3" width="1em"></b-icon>
							<span>Clone</span>
						</div>
					</b-dropdown-item>
					<b-dropdown-item link-class="p-3">
						<div class="d-flex align-items-center">
							<b-icon icon="trash" class="mr-3" width="1em"></b-icon>
							<span>Delete</span>
						</div>
					</b-dropdown-item>
				</b-dropdown>
			</div>
		</div>
	</b-card>
</template>

<script>
	import LinkImageTooltip from "@/components/LinkImageTooltip";

	export default {
		components: { LinkImageTooltip },

		props: { poll: { type: Object, required: true } },

		computed: {
			voted() {
				return this.poll.responders.length;
			},

			remaining() {
				let remaining = { variant: "success", text: "Open indefinitely" };

				if (this.poll.votingWindow == -1) return remaining;

				let createdAt = new Date(this.poll.createdAt).getTime();

				let now = Date.now();

				let expiresAt = createdAt + this.poll.votingWindow * 60 * 1000;

				let diff = expiresAt - now;

				let hours = diff / 1000 / 60 / 60;

				if (hours < 1) {
					remaining.text = `Voting closed`;

					remaining.variant = "danger";

					return remaining;
				}

				hours = Math.round(hours);

				if (hours == 1) {
					remaining.text = `An hour Remaining`;

					remaining.variant = "danger";

					return remaining;
				}

				if (hours < 24) {
					remaining.text = `${hours.toFixed(0)} hours Remaining`;

					remaining.variant = "danger";

					return remaining;
				}

				let days = hours / 24;

				days = Math.round(days);

				if (days == 1) {
					remaining.text = `A day Remaining`;

					remaining.variant = "danger";

					return remaining;
				}

				remaining.text = `${days.toFixed(0)} days Remaining`;

				return remaining;
			},
		},
	};
</script>

<style lang="scss" scoped>
	.remaining {
		background-color: #00876b1a;
		padding: 3px 4px;
		border-radius: 3px;
		&.danger {
			background-color: #ff001a1a;
		}
	}
</style>
